* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Nunito", sans-serif;
}

Button {
  border: none;
  outline: none;
  width: 100%;
  padding: 9px 0;
  padding-left: 50px;
  position: relative;
  color: #fff;
  font-size: 16px;
  letter-spacing: 1px;
  background: #200468;
  cursor: pointer;
  border-radius: 10px;
}

.login {
  width: 100%;
  min-height: 100vh;
  padding: 0 20px;
  background: #e9e9e9;
  display: flex;
}

.login .loginContainer {
  padding: 60px;
  margin: auto;
  border-radius:20px;
  width: 100%;
  max-width: 520px;
  min-height: 600px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background: radial-gradient(
    ellipse at left bottom,
    rgba(61,60,124) 100%,
    rgba(89, 28, 211, 0.9) 59%,
    rgba(61, 78, 167, 0.9) 100%
  );
  box-shadow: 0 50px 70px -20px rgba(0, 0, 0, 0.8);
}
 .steper {
  padding: 60px;
  margin: auto;
  border-radius:20px;
  width: 100%;
  max-width: 520px;
  min-height: 600px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background: radial-gradient(
    ellipse at left bottom,
    rgba(61,60,124) 100%,
    rgba(89, 28, 211, 0.9) 59%,
    rgba(61, 78, 167, 0.9) 100%
  );
  box-shadow: 0 50px 70px -20px rgba(91, 58, 167, 0.8);
}

.col {
  padding: 40px;
  padding-top: 30px;
  margin: auto;
  border-radius:20px;
  width: 100%;
  max-width: 450px;
  max-height: 450px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background: radial-gradient(
    ellipse at left bottom,
    rgb(243, 243, 245) 0%,
    rgba(230, 229, 233, 0.9) 59%,
    rgba(238, 239, 245, 0.9) 100%
  );
  box-shadow: 0 50px 70px -20px rgba(0, 0, 0, 0.8);
}

.login .loginContainer label 
{
  
  color: rgb(236, 232, 232);
  margin: 14px 0;
  display: block;
  font-size: 22px;
  line-height: 1;
}
.steper label {
  color: white;
  margin: 14px 0;
  display: block;
  font-size: 22px;
  line-height: 1;
}

.login .loginContainer input {
  width: 100%;
  border-radius: 20px;
  border: none;
  outline: none;
  font-size: 19px;
  padding: 10px;
  background: rgba(255, 255, 255, 0.1);
  color: #fff;
  letter-spacing: 1px;
  
}
.steper input {
  width: 100%;
  border-radius: 20px;
  border: none;
  outline: none;
  font-size: 19px;
  padding: 10px;
  background: rgba(255, 255, 255, 0.1);
  color: #fff;
  letter-spacing: 1px;
  
}
.steper select {
  width: 100%;
  border-radius: 20px;
  border: none;
  outline: none;
  font-size: 19px;
  padding: 10px;
  background: rgba(255, 255, 255, 0.1);
  color: rgb(113, 115, 131);
  letter-spacing: 1px;
  
}


.login .loginContainer .btnContainer {
  width: 100%;
  padding: 24px 0;
}

.login .loginContainer .btnContainer p {
  margin: 14px 0 0 0;
  text-align: right;
  color: #fff;
}

.login .loginContainer .btnContainer p span {
  color: yellow;
  font-weight: 500;
  letter-spacing: 0.5px;
  margin-left: 5px;
  cursor: pointer;
  transition: all 400ms ease-in-out;
}

.login .loginContainer .btnContainer p span:hover {
  color: red;
}

.login .loginContainer .errorMsg {
  color: red;
  font-size: 16px;
}

.hero {
  width: 100%;
  min-height: 100vh;
  background: radial-gradient(
    ellipse at left bottom,
    rgba(22, 24, 47, 1) 0%,
    rgba(38, 20, 72, 0.9) 59%,
    rgba(17, 27, 75, 0.9) 100%
  );
}

.hero nav {
  background: #e8eaee;
  width: 100%;
  padding: 20px 40px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}


.hero nav h2 {
  color: #fff;
  
}
.hero nav Link{
color: #ffffff;
}
.hero nav button {
  width: 86px;
  padding: 8px 0;
  background: #3D3C7C;
}


.root {
  color: aqua;
}
.column {

  padding: 10px;
   float: left;
   width: 50%;
   padding-left: 15px;
   padding-top: 0%;
}